body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-lexend: Lexend;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-aliceblue: #f3f7fc;
  --color-white: #fff;
  --color-cornflowerblue-100: #327cd0;
  --color-cornflowerblue-200: rgba(50, 124, 208, 0);
  --color-gray-100: #171a1f;
  --color-gray-200: rgba(0, 0, 0, 0);
  --color-silver: #bdc1ca;
  --color-darkslateblue: #1a3e68;
  --color-dimgray: #565d6d;

  /* Border radiuses */
  --br-lg: 18px;
  --br-xl: 20px;
  --br-11xs: 2px;
}
